form {
    width: 90%;
    max-width: 300px;
    background-color: aliceblue;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    margin-bottom: 26px;
}

textarea{
    background-color: aliceblue;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px rgba(32, 35, 41 0.25);
    color: 1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25%);
}

textarea::placeholder{
    color: #A5A5A5;
    font-family: monospace;
    font-weight: 400;
}

textarea:focus{
    outline-color: #6dd6f3;
}

.TodoForm-buttonContainer{
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.TodoForm-button {
    cursor:pointer;
    display:inline-block;
    font-size:20px;
    color:#202329;
    font-weight:400;
    width:120px;
    height:48px;
    border-radius:2px;
    border:none;
    font-family:monospace;
}

.TodoForm-button--add{
    background:#6dd6f3;
    box-shadow:0 5px 25px rgba(90, 214, 249, 0.5); 
}

.TodoForm-button--cancel{
    background: 0 0;
}